import React from 'react';
import d3, { BaseType } from 'd3';
import { Selection } from 'd3-selection';

interface LabelStyle {
  textAnchor: string;
  fill: string;
  stroke: string;
  fontStyle: string;
  fontVariant: string;
  fontWeight: string;
  fontStretch: string;
  lineHeight: string;
  fillOpacity: number;
  fontSize?: number;
}

export interface GaugeProps {
  label: string;
  value: number;
  width: number;
  height: number;
  min: number;
  max: number;
  color: string;
  backgroundColor: string;
  topLabelStyle?: LabelStyle;
  valueLabelStyle?: LabelStyle;
  minMaxLabelStyle?: LabelStyle;
  units?: string;
}

class Gauge extends React.PureComponent<GaugeProps, {}> {
  static defaultProps = {
    label: 'React SVG Gauge',
    min: 0,
    max: 100,
    value: 0,
    width: 400,
    height: 320,
    units: '',
    color: '#fe0400',
    backgroundColor: '#edebeb',
    topLabelStyle: {
      textAnchor: 'middle',
      fill: '#999999',
      stroke: 'none',
      fontStyle: 'normal',
      fontVariant: 'normal',
      fontWeight: 'bold',
      fontStretch: 'normal',
      lineHeight: 'normal',
      fillOpacity: 1,
    },
    valueLabelStyle: {
      textAnchor: 'middle',
      fill: '#010101',
      stroke: 'none',
      fontStyle: 'normal',
      fontVariant: 'normal',
      fontWeight: 'bold',
      fontStretch: 'normal',
      lineHeight: 'normal',
      fillOpacity: 1,
    },
    minMaxLabelStyle: {
      textAnchor: 'middle',
      fill: '#999999',
      stroke: 'none',
      fontStyle: 'normal',
      fontVariant: 'normal',
      fontWeight: 'normal',
      fontStretch: 'normal',
      lineHeight: 'normal',
      fillOpacity: 1,
    },
  };

  oldValue = 0;

  _getSmallerDimension = () => {
    return this.props.height < this.props.width
      ? this.props.height
      : this.props.width;
  };

  _getPathValues = (value: number) => {
    const dx = 0;
    const dy = 0;
    const smallerDimension = this._getSmallerDimension();

    const getAlpha = (fra: number) => {
      return (1 - fra) * Math.PI;
    };
    let alpha;
    const numVal = Number(value);
    const numMin = Number(this.props.min);
    const numMax = Number(this.props.max);
    if (numVal > numMax) {
      alpha = getAlpha(1);
    } else if (numVal < numMin) {
      alpha = getAlpha(0);
    } else if (numMin < 0 && numMax < 0) {
      if (numVal < numMin) {
        alpha = getAlpha(0);
      } else {
        alpha = getAlpha((-numMin + numVal) / (-numMin + numMax));
      }
    } else if (numMin < 0 && numMax >= 0) {
      if (numVal < numMin) {
        alpha = getAlpha(0);
      } else if (numVal < 0) {
        alpha = getAlpha((-numMin + numVal) / (-numMin + numMax));
      } else {
        alpha = getAlpha((-numMin + numVal) / (-numMin + numMax));
      }
    } else {
      alpha = getAlpha(numVal / numMax);
    }

    const Ro =
      this.props.height * 1.3 < this.props.width
        ? this.props.height / 1.4 - this.props.height / 10
        : this.props.width / 2 - this.props.width / 10;
    const Ri = Ro - smallerDimension / 5;

    const Cx = this.props.width / 2 + dx;
    const Cy = this.props.height / 1.25 + dy;

    const Xo = this.props.width / 2 + dx + Ro * Math.cos(alpha);
    const Yo =
      this.props.height - (this.props.height - Cy) - Ro * Math.sin(alpha);
    const Xi = this.props.width / 2 + dx + Ri * Math.cos(alpha);
    const Yi =
      this.props.height - (this.props.height - Cy) - Ri * Math.sin(alpha);

    return { alpha, Ro, Ri, Cx, Cy, Xo, Yo, Xi, Yi };
  };

  _getPath = (value: number) => {
    const { Ro, Ri, Cx, Cy, Xo, Yo, Xi, Yi } = this._getPathValues(value);

    let path = 'M' + (Cx - Ri) + ',' + Cy + ' ';
    path += 'L' + (Cx - Ro) + ',' + Cy + ' ';
    path += 'A' + Ro + ',' + Ro + ' 0 0 1 ' + Xo + ',' + Yo + ' ';
    path += 'L' + Xi + ',' + Yi + ' ';
    path += 'A' + Ri + ',' + Ri + ' 0 0 0 ' + (Cx - Ri) + ',' + Cy + ' ';
    path += 'Z ';
    return path;
  };

  componentDidMount() {
    return this.renderRadialGauge();
  }

  componentDidUpdate() {
    return this.renderRadialGauge();
  }

  componentWillReceiveProps() {
    this.oldValue = this._getPercentage(
      Number(this.props.value),
      this.props.max,
      this.props.min,
    );
    this._getPercentage(this.props.value, this.props.max, this.props.min);
  }

  renderRadialGauge() {
    const value = typeof this.props.value === 'object' ? '' : this.props.value;
    const smallerDimension = this._getSmallerDimension();

    const topLabelStyle = this.props.topLabelStyle.fontSize
      ? this.props.topLabelStyle
      : { ...this.props.topLabelStyle, fontSize: smallerDimension / 10 };
    const valueLabelStyle = this.props.valueLabelStyle.fontSize
      ? this.props.valueLabelStyle
      : { ...this.props.valueLabelStyle, fontSize: smallerDimension / 4 };
    const minMaxLabelStyle = this.props.minMaxLabelStyle.fontSize
      ? this.props.minMaxLabelStyle
      : { ...this.props.minMaxLabelStyle, fontSize: smallerDimension / 12 };
    const { Cx, Ro, Ri, Xo, Cy, Xi } = this._getPathValues(this.props.max);

    interface SVGStyle {
      width: number;
      height: number;
      overflow: string;
      position: string;
      left: number;
      top: number;
    }

    const svgStyle: SVGStyle = {
      width: this.props.width,
      height: this.props.height,
      overflow: 'hidden',
      position: 'relative',
      left: 0,
      top: 0,
    };

    // @ts-ignore - el exists on class
    const el = this.el;

    while (el.firstChild) {
      el.removeChild(el.firstChild);
    }

    const svg = d3
      .select(el)
      .append('svg')
      .attr('width', this.props.width)
      .attr('height', this.props.height)
      // @ts-ignore
      .style(svgStyle);

    // Max portion
    svg
      .append('path')
      .attr('fill', this.props.backgroundColor)
      .attr('stroke', 'none')
      .attr('d', this._getPath(this.props.max));

    // Label
    this._renderLabel(
      svg,
      this.props.width / 2,
      this.props.height / 8,
      topLabelStyle,
      this.props.label,
    );

    // Value label
    this._renderLabel(
      svg,
      this.props.width / 2,
      this.props.height / 5 * 4.2,
      valueLabelStyle,
      value,
      this.props.units,
    );

    // Min label
    this._renderLabel(
      svg,
      (Cx - Ro + (Cx - Ri)) / 2,
      Cy + smallerDimension / 10,
      minMaxLabelStyle,
      this.props.min,
    );

    // Max label
    this._renderLabel(
      svg,
      (Xo + Xi) / 2,
      Cy + smallerDimension / 10,
      minMaxLabelStyle,
      this.props.max,
    );

    this._drawGauge(svg, this.oldValue, this.props.color);

    this._animateGauge(svg, Number(value), this.oldValue);

    svg.transition();
    return svg;
  }

  render() {
    return (
      <div
        ref={(el) => {
          // @ts-ignore - el exists
          this.el = el;
        }}
      />
    );
  }

  _renderLabel = (
    el: Selection<BaseType, {}, null, undefined>,
    x: number,
    y: number,
    style: LabelStyle,
    text: number | string,
    units?: string,
  ) => {
    return (
      el
        .append('text')
        .attr('x', x)
        .attr('y', y)
        // @ts-ignore
        .style(style)
        // These styles have to be added individually because d3 can't seem to recognize inline styles
        .style('text-anchor', style.textAnchor)
        .style('font-size', style.fontSize)
        .style('font-style', style.fontStyle)
        .style('font-variant', style.fontVariant)
        .style('font-weight', style.fontWeight)
        .style('font-stretch', style.fontStretch)
        .style('line-height', style.lineHeight)
        .style('fill-opacity', style.fillOpacity)
        .text(text)
        .append('tspan')
        .text(units)
        .style('font-size', style.fontSize / 2)
    );
  };

  _animateGauge = (
    el: Selection<BaseType, {}, null, undefined>,
    value: number,
    oldValue: number,
  ) => {
    // @ts-ignore
    // tslint:disable-next-line
    const scope = this;
    let progress;
    return (
      el
        .transition()
        .delay(250)
        // .ease('elastic') // Not sure if we should use this effect
        .duration(700)
        .selectAll('.gauge-value')
        .tween('progress', () => {
          return function(t: number) {
            progress = oldValue + t * (value - oldValue);
            return d3.select(this).attr('d', scope._getPath(progress));
          };
        })
    );
  };

  _drawGauge = (
    el: Selection<BaseType, {}, null, undefined>,
    value: number,
    color: string,
  ) => {
    return el
      .append('path')
      .attr('class', 'gauge-value')
      .attr('fill', color)
      .attr('stroke', 'none')
      .attr('d', this._getPath(value));
  };

  _getPercentage = (dataVal: number, maxVal: number, minVal: number) => {
    if (dataVal && !isNaN(dataVal)) {
      const val = Number(dataVal);
      const max = Number(maxVal);
      const min = Number(minVal);
      if (val > max) {
        return 100;
      }
      if (val < min) {
        return 0;
      }
      if (min < 0 && max < 0) {
        if (val < min) {
          return 0;
        } else {
          return (-min + val) / (-min + max) * 100;
        }
      }
      if (min < 0 && max >= 0) {
        if (val < min) {
          return 0;
        } else if (val < 0) {
          return (-min + val) / (-min + max) * 100;
        } else {
          return (-min + val) / (-min + max) * 100;
        }
      }
      return val / max * 100;
    }
    return 0;
  };
}

export default Gauge;



